*, *:before, *:after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  body {
    overflow: hidden;
    font-family: Helvetica, Arial, sans-serif;
  }
  $numOfSlides: 5;
  $slidingAT: 1.3s;
  $controlSize: 60px;

  .b--clip-slider-a{
      
      position: relative;
      height: 100vh;

      &__list-group {
        z-index: 1;
        position: relative;
        height: 100%;
    
        &__list-item{
          overflow: hidden;
          position: absolute;
          left: 50%;
          top: 50%;
          width: 150vmax;
          height: 150vmax;
          transform: translate(-50%, -50%);
          border-radius: 50%;
          transition: clip-path 0s $slidingAT*0.7;
          clip-path: circle(0px at 78vw);
          
        }
      }
      &__controls{
        
        &__item{
          z-index: 50;
          position:absolute;
          top:50%;
          width:30px;
          height: 30px;
          &--prev{
            background:magenta;
            left:20%;
          }
          &--next{
            left: 90%;
            background:green;
          }
        }
      }
      &__pagination{

        position: absolute;
        bottom: 0;
        width: 100%;
        left: 0;
        text-align: center;
        z-index: 50;
        padding: 1rem 1rem;
        // pagination-item 
        &__item{
          display:inline-block;
          width: 30px;
          height: 30px;
          background: #fff;
          display: inline-block;
          border-radius: 50%;
          margin-right: 1rem;
          &--active{
            background: #2da6ff;
          }
        }
      }

  }
  

  
  //slide itfself
  .b--card-a{
    position: absolute;
    left: 50%;
    top: 50%;
    width: 100vw;
    height: 100vh;
    margin-left: -50vw;
    margin-top: -50vh;
    background-size: cover;
    background-position: center center;

    &:before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,0.3);
    }
    &__content{
      position: absolute;
      left: 5%;
      top: 50%;
      margin-left: $controlSize*-.5;
      margin-top: $controlSize*0.8;
      max-width: 480px;
      color: #fff;

      &__title {
        margin-bottom: 20px;
        font-size: 60px;
      }
      &__subtitle {
        font-size: 25px;
      }
    }
  }
  
  .s--active{
    z-index: 1;
    transition: clip-path $slidingAT;
    clip-path: circle(120vmax at 78% 50%);
  }

  .slider_control{
    
  }

